.resultados{
    width: 95%;
    display: flex;
    margin: auto;
    flex-direction: column;
    .finales{
        width: fit-content;
        display: flex;
        margin:1% auto 0 auto;
        .parte{
            background: rgb(216, 0, 0);
            height: 50px;
            display: flex;
          
            p{
                color: rgb(252, 182, 182);
                font-size: 1.5rem;
                margin: auto;
            }
            &.uno{
                background: rgb(56, 0, 0);
                width: 100px;
            }
            &.dos{
                width:150px;
            }
        }
    }
}
.tiros{
    width: 95%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin:10px auto;
    .caja1{
        width: 100%;
        display: flex;
        background: rgb(59, 0, 0);
        height: 50px;
        .col{
            display: flex;
            border: 1px solid rgb(27, 1, 1);
            p{
                margin: auto;
            }
            &.unos{
                width: 10%;
            }
            &.doss{
                width: 20%;
            }
            &.tress{
                width: 100% - 20% - 10%;
            }
        }
    }
    .fila{
        display: flex;
        min-height: 40px;
        .col{
            display: flex;
            border: 1px solid rgb(48, 2, 2);
            p{
                margin: auto;
                font-size: 1.1rem;
            }
            &.unos{
                width: 10%;
                
            }
            &.doss{
                width: 20%;
            }
            &.tress{
                width: 100% - 20% - 10%;
                p{
                    margin: auto auto auto 5%;
                    letter-spacing: 5px;
                }
            }
        }
        &.color1{
            background: rgb(116, 5, 5);
        }
        &.color2{
            background: rgb(158, 3, 3);
        }
        &:hover{
            background: rgb(39, 2, 2);
        }
    }
}